import React, { useState, useEffect } from 'react';
import { sampleData } from '../../sampleData/sampleEvents';
import './style.scss';
import { IoIosArrowBack } from "react-icons/io";
import calendar from '../../assets/icon/ico_calendar-white.png';
import location from '../../assets/icon/ico_location-white.png';
import { Link } from 'react-router-dom';
import { IconContext } from "react-icons";
import { MdKeyboardArrowUp, MdKeyboardArrowDown } from "react-icons/md";
import moment from 'moment';
import { Drawer } from '@mui/material';
//import { sampleABOs } from '../../sampleData/sampleABOs';
import PhoneInput from 'react-phone-number-input'
import 'react-phone-number-input/style.css'

const Register = (props) => {
    console.log(props)
    const [displayBio, setdisplayBio] = useState(false);
    const [event, setEvent] = useState(null);
    const [add, setAdd] = useState(false);
    // const [name, setName] = useState();
    // const [email, setEmail] = useState();
    //const [value, setValue] = useState();
    const [change, setChange] = useState(true);

    const [attendee, setAttendee] = useState({
        type: '',
        name: '',
        email: '',
        phone: '',
        survey: ''
    })

    const [attendeeList, setAttendeeList] = useState([])


    useEffect(() => {

        sampleData.map(event => {
            event.start = new Date(event.startTime);
            event.end = new Date(event.endTime);
            event.startDate = moment(event.start).format("dddd, MMMM Do YYYY");
            event.eventStart = moment(event.start).format("HH:mm");
            event.eventEnd = moment(event.end).format("HH:mm");
            event.eventRange = `${event.eventStart} - ${event.eventEnd}`
            event.lang.map((reg, index) => {

                if (reg === 'en-my') {
                    event.lang[index] = ' English '
                } else if (reg === 'ms') {
                    event.lang[index] = ' Bahasa Melayu '
                } else if (reg === 'zh') {
                    event.lang[index] = ' Chinese '
                } //lang: ['en-my', 'ms', 'zh'], 'en-my' isn't object. [{'en-my'},{'ms'}], return 'English'
            });
        })

        const id = props.match.params.id
        console.log(id, parseInt(id)); //parseInt is convert id to integer
        let thisEvent = sampleData.find(e => e.id === parseInt(id)) //e.id === id is different data type, so need parseInt
        setEvent(thisEvent);

        console.log(thisEvent);
        console.log(sampleData)

        // get attendees from localstorage
        let attendeeList = JSON.parse(localStorage.getItem('attendees')) || [];
        setAttendeeList(attendeeList)

    }, []); //first action after return

    useEffect(() => {
        console.log(attendee)
    }, [attendee])

    const handleAttendeeInput = (e, phoneId = '') => {
        console.log(e, phoneId)
        let id = phoneId ? phoneId : e.target.id;
        let value = phoneId ? e : e.target.value


        setAttendee({
            ...attendee,
            [id]: value
        })
    }

    const handleAdd = () => {

        let currentList = JSON.parse(localStorage.getItem('attendees')) || [];
        currentList.push(attendee)

        localStorage.setItem('attendees', JSON.stringify(currentList));
        setAttendeeList(currentList)
        console.log(attendee);
        setAdd(false);
    }

    const handleRemove = (j,value) => {

        

        let currentList = JSON.parse(localStorage.getItem('attendees')) || [];

        localStorage.setItem('attendees', JSON.stringify(currentList));
        setAttendeeList(currentList)
        console.log(attendee);
        console.log(j,value)
    }

    return (
        <>
            {event && <div className='event-register'>
                <div className='mobile-header'>
                    <Link to={`/module/${event.id}`}>
                        <IconContext.Provider value={{ size: "2em" }}>
                            <div className='arrow-back-transparent'><IoIosArrowBack /></div>
                        </IconContext.Provider>
                    </Link>
                    <div className='title'>
                        Register
                    </div>
                    <div className='page-title'>
                        Register
                    </div>
                </div>
                <div className='event-details'>
                    <div className='event-wrapper'>
                        <div className='event-reg-card'>
                            <div className='event-reg-card-wrapper'>
                                <div className='event-reg-card-title'>{event.title}</div>
                                {displayBio ? (
                                    <IconContext.Provider value={{ size: "2em" }}>
                                        <div className='event-reg-card-toggle' onClick={() => setdisplayBio(false)}> <MdKeyboardArrowDown /> </div>
                                    </IconContext.Provider>
                                ) : (<div className='event-item'>
                                    <div className='event-reg-card-language'>{event.lang.join()}</div>
                                    <div className='event-reg-card-item'>
                                        <div className='icon'><img src={calendar} /></div>
                                        <div className='info'>{event.startDate}<br />{event.eventRange}</div>
                                    </div>
                                    <div className='event-reg-card-item'>
                                        <div className='icon'><img src={location} /></div>
                                        <div className='info'>Hosted virtually via Zoom</div>
                                    </div>
                                    <br /><IconContext.Provider value={{ size: "2em" }}>
                                        <div className='event-reg-card-toggle' onClick={() => setdisplayBio(true)}><MdKeyboardArrowUp /></div>
                                    </IconContext.Provider>
                                </div>)
                                }
                                <div className='ticket'>
                                    <div className='price'>FREE</div>
                                </div>
                            </div>
                        </div>

                        <div className='attendance-card'>
                            <div className='att-header'>
                                <div className='att-header-title'>Who s Going</div>
                                <div className='att-header-add' onClick={() => setAdd(true)}>Add Attendee</div>
                            </div>
                            {attendeeList.map((abos, j) => {
                                return (
                                    <div key={j} className='att-cards'>
                                        <div className='att-card'>
                                            <div className='att-card-top'>
                                                <div className='att-card-title'></div>
                                                <div className='att-card-remove' onClick={e => handleRemove(e.target.value)}>Remove</div>
                                            </div>
                                            <div className='att-card-name att-item'>{abos.name}
                                            </div>
                                            <div className='att-card-name att-item'>{abos.email}
                                            </div>
                                            <div className='att-card-name att-item'>{abos.phone}
                                            </div>
                                        </div>
                                    </div>)
                            })}
                        </div>
                    </div>
                </div>
                <Link to={`/module/${event.id}/registered`}>
                    <div className='event-confirm-mobile'>
                        <div className='amway-button'>Confirm</div>
                    </div>
                </Link>
                <Drawer open={add} className="event-addattendee">
                    <div className='add-attendee'>
                        <div className='add-attendee-header'>Add Attendee</div>
                        <div className='add-attendee-details'>
                            <div className='add-attendee-details-toggle'>
                                <div className={`add-attendee-details-toggle-pill ${change ? "active" : ""}`} onClick={() => setChange(true)}>Business</div>
                                <div className={`add-attendee-details-toggle-pill ${change ? "" : "active"}`} onClick={() => setChange(false)}>Guest</div>
                            </div>
                            <div className='add-attendee-details-form'>
                                <div className='add-attendee-details-form-guest'>
                                    <input id='name' placeholder='Full name' value={attendee.name} onChange={handleAttendeeInput} />
                                    <input id='email' placeholder='Email address' type='email' value={attendee.email} onChange={handleAttendeeInput} />
                                    <PhoneInput

                                        placeholder="123-456-789"
                                        value={attendee.phone}
                                        onChange={(val) => handleAttendeeInput(val, 'phone')} />
                                    <select id="survey" value={attendee.survey} onChange={handleAttendeeInput} >
                                        <option>How do you know your guest?</option>
                                        <option value="1">Option 1</option>
                                        <option value="2">Option 2</option>
                                    </select>
                                    <div className='import'>Import from Contacts</div>
                                </div>
                            </div>

                            <div className='add-attendee-buttons'>
                                <div className='add-button' onClick={() => setAdd(false)}>Cancel</div>
                                <div className={`add-button ${attendee.name ? "" : "gray"}`} onClick={handleAdd}>Add</div>
                            </div>
                        </div>
                    </div>
                </Drawer>
            </div>}

        </>
    )
}

export default Register;